@import url(https://fonts.googleapis.com/css2?family=Edu+TAS+Beginner:wght@400;500&family=Roboto:wght@300&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    /* overflow: hidden; */
    font-family: 'Roboto', cursive;
}
.Palette{
    height: 100vh;
}

.Palette-box{
    height: 90%;
    width: 100%;
}
.Palette-footer{
    font-weight: 900;
    height: 5vh;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 20px;
}


.ColorBox{
    display: inline-block;
    position: relative;
    width: 20%;
    height: 25%;
}
.SingleColorPalette .ColorBox{
    height: 50%;
}

.btn-cpy,
.btn-goback
{
    color: white;
    position: absolute;
    padding: 10px 20px;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    outline: none;
    background: rgba(255, 255, 255, 0.3);
    border: none;
    font-size: 1.2rem;
    text-decoration: none;
    border: none;
}
.ColorBox.go-back{
    background-color: black;
}
.btn-cpy{
    opacity: 0;
}


.ColorBox:hover .btn-cpy{
    opacity: 1;
}

.color-title{
    position: absolute;
    bottom: 0;
    left: 0;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 5px;
    font-size: 12px;
    color: black;
}

.light-title
{
    color: white;
}

.seemore{
    color: white;
    text-transform: uppercase;
    position: absolute;
    bottom: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.3);
    padding: 5px;
}
.dark-title
{
    color: rgba(0, 0, 0, 0.5);
}

.overlay{
    position: fixed;
    opacity: 0;
    font-size: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: white;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
}

.overlay.expand{
    display: fixed;
    -webkit-transform: scale(1);
            transform: scale(1);
    text-align: center;
    opacity: 1;
    z-index: 25;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    -webkit-transition-delay: 0.3s;
            transition-delay: 0.3s;
}

.overlay h1{
    width: 100%;
    background: rgba(255, 255, 255, 0.3);
}

.overlay p{
    font-weight: 100;
    font-size: 2rem;
}



.slider-container{
    display: flex;
}
.slider{
    width: 340px;
    margin: 0 10px;
    display: inline-block;
}
.rc-slider-track{
    background-color: transparent;
}
.rc-slider-rail{
    height: 8px;
}
.rc-slider-handle,
.rc-slider-handle:focus,
.rc-slider-handle:hover,
.rc-slider-handle:active
{
    background-color: green;
    border: 2px solid green;
    outline: none;
    width: 13px;
    height: 13px;
    margin-left: -7px;
    margin-top: -3px;
}
.Navbar{
    height: 5vh;
    display: flex;
    align-items: center;
}

.Navbar *{
    margin-right: 10px;
}

.Logo{
    height: 100%;
    display: flex;
    align-items: center;
    background-color: lightgray;
    padding: 0 20px;
    color: inherit;
    text-decoration: none;
}
.select-item{
    margin-left: auto;
}

.Palettelist
{
    background-color: blue;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.PaletteContainer
{
    width: 50%;
    display: flex;
    flex-direction: column;
}
.Palettelistnav
{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}
.Palettelistnav h3{
    font-size: 3rem;
}
.Palettelistnav a{
    text-decoration: underline;
    color: white;
}

.PalettesBox
{
    display: grid;
    grid-template-columns: repeat(3, 30%);
    grid-gap: 5%;
}
.MiniPalette
{
    background-color: white;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
}
.MiniColors
{
    height: 150px;
    width: 100%;
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}
.MiniColor:nth-child(1)
{
    border-top-left-radius: 5px;
}
.MiniColor:nth-child(5)
{
    border-top-right-radius: 5px;

}
.MiniColor:nth-child(16)
{
    border-bottom-left-radius: 5px;
}
.MiniColor:nth-child(20)
{
    border-bottom-right-radius: 5px;
}
.MiniColor
{
    width: 20%;
    height: 25%;
    display: inline-block;
    margin: 0 auto;
    position: relative;
    margin-bottom: -3.5px;
}
.MiniPaletteBar
{
    display: flex;
    justify-content: space-between;
    padding: 5px;
}
